@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;500&display=swap);
body{margin:0;font-family:"Roboto",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}.main-container{margin:0;padding:0;display:flex;flex-direction:column;justify-content:center;align-items:center;height:100vh}.previewer-container{width:920px;height:500px;margin:32px}@media(max-width: 767.98px){.previewer-container{width:100%}}.footer{text-align:center}.footer p{font-size:14px;margin:3px;padding:2px}
:root{--border-color: rgba(85, 84, 84, 0.103)}.container{display:flex;width:100%;height:100%;box-shadow:0 0 12px rgba(85, 84, 84, 0.103);box-shadow:0 0 12px var(--border-color);overflow:hidden}.container>div{display:flex;flex-direction:column;width:50%;height:100%;margin:0;overflow:hidden;box-sizing:border-box;transition:ease-in-out;transition-duration:.5s}.container .expanded{width:100%}.container .editor-collapsed{width:0;transform:translate(-100%)}.container .preview-collapsed{width:0;transform:translate(100%)}@media(max-width: 767.98px){.container{flex-direction:column}.container>div{width:100%}}.editor,.preview{width:100%;margin:0;padding:8px;flex:1 1;overflow-y:auto;background-color:#fff;resize:none;border:none;box-sizing:border-box}.editor:focus,.preview:focus{outline:none}.header{width:100%;font-size:1.2em;display:flex;padding:.3em;background-color:#fff;box-sizing:border-box;border:1px solid;border-color:transparent transparent rgba(85, 84, 84, 0.103) transparent;border-color:transparent transparent var(--border-color) transparent;color:rgba(119,118,118,.808)}.header button{all:unset;-webkit-text-fill-color:#000;cursor:pointer}.header button:focus{outline:none}.header .icon{margin:0 .5em}.header p{margin:0;padding:0;flex:1 1}.preview{border:1px solid;border-color:transparent transparent transparent rgba(85, 84, 84, 0.103);border-color:transparent transparent transparent var(--border-color)}.header-preview{border-color:transparent transparent rgba(85, 84, 84, 0.103) rgba(85, 84, 84, 0.103);border-color:transparent transparent var(--border-color) var(--border-color)}@media(max-width: 767.98px){.expand{display:none}}
